import { AbstractComponent, AbstractComponentMethod, AbstractComponentParameter } from "../../abstractcomponent/abstractcomponent";

/**
 * 通用构件元数据
 */
export class CommonComponent extends AbstractComponent{
    public getEmptyMethodInstance(): AbstractComponentMethod {
        return new ComponentMethod();
    }
}

/**
 * 通用构件方法
 */
export class ComponentMethod extends AbstractComponentMethod{
    public getEmptyParamterInstance(): AbstractComponentParameter {
        return new ComponentParameter();
    }
}

/**
 * 通用构件参数
 */
export class ComponentParameter extends AbstractComponentParameter{
}


