export class CommonObject{
    Id:string;
    Code:string;
    Name:string;
}

export interface ObjectDict<T>{
    [K:string]:T;
}

export const typeList:TypeObject[]=[
    {text:'int',value:'int'},
    {text:'string',value:'string'},
    {text:'double',value:'double'},
    {text:'boolean',value:'boolean'},
    {text:'dateTime',value: 'dateTime'},
    {text:'decimal',value: 'decimal'}
];

export class TypeObject{
    text:any;
    value:any
}


