import {ComponentFactoryResolver, Injector, Type} from '@angular/core';
import { CommonComponentDesignerPackageView } from './views';
import { CommonComponentDesignerComponent } from './views/ccmpdesigner/ccmp.component';
import { Package, GSP } from '@farris/ide-devkit';

export class CommonComponentDesignerPackage extends Package {

  private layoutPanel: any;

  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {
    this.gsp.workspace.addOpener((uri: string) => this.openSg(uri));
  }

  openSg(uri) {
    if (uri.endsWith('.ccmp')) {
      const pkgView = new CommonComponentDesignerPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as CommonComponentDesignerComponent;
      currentElement.metadataUri = uri;
      const fileName = uri.substring(uri.lastIndexOf('/') + 1);
      return {
        getTitle: () => fileName,
        getElement: () => pkgView.getElement()
      };
    }
  }
}



