import { Component, OnInit, ViewChild } from '@angular/core';
import { Headers, RequestOptions, Http } from '@angular/http';
import { isNullOrUndefined } from 'util';
import { host } from './commonobject/global';
import { DialogComponent } from '@farris/ui';
import { CommonObject, ObjectDict, TypeObject, typeList } from './commonobject/commonobject';
import { process, State } from '@progress/kendo-data-query';
import { AssemblyInfo } from './commonobject/assemblytreeobject';
import { CommonComponent, ComponentMethod } from './ccmpmetadata/ccmpmetadata';
import { MetadataDto, GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { GridDataResult, DataStateChangeEvent } from '@progress/kendo-angular-grid';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';
import { CacheService } from '../../../../../reference/farris/farris.idedevkit.ref';

@Component({
  selector: 'app-cmp',
  templateUrl: './ccmp.component.html',
  styleUrls: ['./ccmp.component.css']
})
export class CommonComponentDesignerComponent implements OnInit {

  /**构件元数据 */
  cmpmetadata: CommonComponent;

  /**元数据抽象Dto */
  metadataDto: MetadataDto;

  /**程序集名称列表 */
  assemblyNameList: string[];

  /**程序集列表，封装成对象 */
  assemblyNameData: CommonObject[] = []; 

  /**已经选择的程序集对象 */
  selectedAssemblyName: CommonObject;

  /**已经选择的程序集名称 */
  selectedClassName: string;

  /**按编号所选的程序集数组 */
  selectedCodes: string[] = [];

  /**程序集数据源 */
  assemblyTree: AssemblyInfo[] = [];
  selectedKeys: any[] = [];
  expandedKeys: any[] = [];
  isItemSelected = (_: any, index: string) => this.selectedKeys.indexOf(index) > -1;
  isshow = false;
  metadata: CommonComponent;
  state: State = {
    filter: {
      logic: 'and',
      filters: [{ field: 'code', operator: 'contains', value: '' }]
    }
  };
  gridData: GridDataResult = process(this.assemblyNameData, this.state);
  showDialog:boolean=false;

  @ViewChild('fdlg') fdialog: DialogComponent;
  @ViewChild('fdlg1') fdialog1: DialogComponent;

  private _metadataUri: string;
  set metadataUri(metadataUri: string) {
    const fileName = metadataUri.substring(metadataUri.lastIndexOf('/') + 1);
    const filePath = metadataUri.substring(1, metadataUri.lastIndexOf('/'));
    this.metadataService.LoadMetadata(fileName, filePath).subscribe(value => {
      this.metadataContent = value;
    });
   // this.loadCmpMetadata(dto);
  }

  set metadataContent(metadataContent: any) {
    this.loadCmpMetadata(metadataContent);
  }

  constructor(private http: Http, private metadataService: GSPMetadataService,private cache:CacheService) { }

  ngOnInit() {
    this.cmpmetadata=new CommonComponent();
    this.assemblyNameData = [];
    this.assemblyNameList = [];
  }

  /**
   * 加载通用构件元数据
   * @param dto 元数据MetadataDto
   */
  private loadCmpMetadata(dto: MetadataDto) {
    this.metadataDto = dto;
    if (dto.content && dto.content != "") {
      this.cmpmetadata=new CommonComponent();
      let jsonObj = JSON.parse(dto.content);
      this.cmpmetadata.init(jsonObj);
      this.cmpmetadata.Code=(isNullOrUndefined(this.cmpmetadata.Code)||isNullOrEmptyString(this.cmpmetadata.Code))?dto.code:this.cmpmetadata.Code;
      this.cmpmetadata.Name=(isNullOrUndefined(this.cmpmetadata.Name)||isNullOrEmptyString(this.cmpmetadata.Name))?dto.name:this.cmpmetadata.Name;
    }
  }

  /**
   * 获取程序集列表
   */
  getAssemblyNameList() {
    this.state = {
      filter: {
        logic: 'and',
        filters: [{ field: 'code', operator: 'contains', value: '' }]
      }
    };
    this.assemblyTree = [];
    this.assemblyNameData = [];
    this.fdialog.title = '程序集列表';
    this.fdialog.height = 520;
    this.fdialog.width = 400;
    this.fdialog.show();
    if (this.assemblyNameList.length > 0) {
      this.assemblyNameList.forEach(element => {
        let ass = {} as CommonObject;
        ass.Code = element;
        this.assemblyNameData.push(ass);
        this.gridData = process(this.assemblyNameData, this.state);
      });
    }
    else {
      let url = host + "/api/dev/main/v1.0/AssemblyService";
      this.http.get(url).toPromise().then(response => {
        if (response.status == 200) {
          let assemblies = response.json().value;
          if (assemblies && assemblies.length > 0) {
            assemblies.forEach(element => {
              let ass = {} as CommonObject;
              ass.Code = element;
              this.assemblyNameData.push(ass);
              this.gridData = process(this.assemblyNameData, this.state);
            });
          }
        }
      })
    }
  }

  private onSelectedKeysChange(e) {
    this.selectedAssemblyName = {} as CommonObject;
    let len = this.selectedCodes.length;
    if (len > 0) {
      for (let i = 0; i < len; i++) {
        let tempIndex = this.assemblyNameData.findIndex(value => value.Code == this.selectedCodes[i]);
        if (tempIndex != (-1)) {
          this.selectedAssemblyName = this.assemblyNameData[tempIndex];
        }
      }
    }
  }

  private assemblySelectConfirm() {
    if(isNullOrUndefined(this.selectedAssemblyName.Code)||isNullOrEmptyString(this.selectedAssemblyName.Code)){
      alert("请选择程序集");
      return;
    }
    this.fdialog.close();
    this.fdialog1.title = '反射列表';
    this.fdialog1.height = 600;
    this.fdialog1.width = 600;
    this.fdialog1.show();
    let url = host + "/api/dev/main/v1.0/AssemblyInfoService(assemblyName='" + this.selectedAssemblyName.Code + "')";
    this.http.get(url).toPromise().then(response => {
      if (response.status == 200) {
        this.assemblyTree = [];
        var temp = response.json();
        temp.Code = temp.Code + ".dll";
        this.assemblyTree.push(temp);
        let length = this.assemblyTree.length;
        this.expandedKeys = [];
        for (let i = 0; i < length; i++) {
          this.expandedKeys.push(String(i));
        }
      }
    })
  }

  public handleSelection({ index, dataItem }: any): void {
    this.selectedClassName = undefined;
    this.selectedKeys = [index];
    if (index.split('_').length == 3) {
      this.selectedClassName = dataItem.Code;
    }
  }

  private classSelectConfirm() {
    if (this.selectedClassName == undefined) {
      alert("请选择需要加载的类。");
      return;
    }
    let url=host+"/api/dev/main/v1.0/MethodService(assemblyName='"+ this.selectedAssemblyName.Code.replace(".dll", "")+"',classFullName='"+this.selectedClassName+"')";
    this.http.get(url).toPromise().then(response => {
      this.fdialog1.close();
      if (response.status == 200) {
        let assemblyNameList=new Array<string>();
        assemblyNameList=response.json().value;
        let methods=[] as  Array<ComponentMethod>;
        if(assemblyNameList!=null&&assemblyNameList.length>0){
          assemblyNameList.forEach(assName => {
              let method=new ComponentMethod();
              method.init(JSON.parse(assName));
              methods.push(method);
          });
        }
        this.cmpmetadata.AssemblyName = this.selectedAssemblyName.Code.replace(".dll", "");
        this.cmpmetadata.FullClassName = this.selectedClassName;
        this.cmpmetadata.Methods = methods;
        // this.getComponentModel(this.cmpmetadata);
        this.cmpmetadata = Object.assign(new CommonComponent(), this.cmpmetadata);
      }
    })
  }

  getComponentModel(metadata: CommonComponent) {
    // metadata.models = [] as CommonComponentModel[];
    // if (this.cmpmetadata.methods && this.cmpmetadata.methods.length > 0) {
    //   this.cmpmetadata.methods.forEach(method => {
    //     if (method.parameters && method.parameters.length > 0) {
    //       method.parameters.forEach(param => {
    //         if (!param.isBasic) {
    //           let isExist = false;
    //           if (metadata.models.length > 0) {
    //             let tempIndex = metadata.models.findIndex(value => value.modelId == param.modelId);
    //             if (tempIndex != (-1)) {
    //               isExist = true;
    //             }
    //           }
    //           if (isExist == false) {
    //             let model = {} as CommonComponentModel;
    //             model.id = param.modelId;
    //             model.code = param.modelName;
    //             model.modelType = CommonComponentModelType.CM;
    //             model.name = param.modelName;
    //             model.modelId = param.modelId;
    //             metadata.models.push(model);
    //           }
    //         }
    //       });
    //     }
    //   });
    // }
  }

  reloadAssembly() {
    if (isNullOrUndefined(this.cmpmetadata.AssemblyName) || isNullOrEmptyString(this.cmpmetadata.AssemblyName)) {
      alert("程序集名称为空，请从程序集加载构件方法。");
      return;
    }
    if (isNullOrUndefined(this.cmpmetadata.FullClassName) || isNullOrEmptyString(this.cmpmetadata.FullClassName)) {
      alert("类全名称为空，请从程序集加载构件方法。");
      return;
    }
    let url=host+"/api/dev/main/v1.0/MethodService(assemblyName='"+ this.cmpmetadata.AssemblyName+"',classFullName='"+this.cmpmetadata.FullClassName+"')";
    this.http.get(url).toPromise().then(response => {
      if (response.status == 200) {
        let assemblyNameList=new Array<string>();
        assemblyNameList=response.json().value;
        let methods=[] as Array<ComponentMethod>;
        if(assemblyNameList!=null&&assemblyNameList.length>0){
          assemblyNameList.forEach(assName => {
              let method=new ComponentMethod();
              method.init(JSON.parse(assName));
              methods.push(method);
          });
        }
        this.cmpmetadata.Methods = methods;
        // this.getComponentModel(this.cmpmetadata);
        this.cmpmetadata = Object.assign(new CommonComponent(), this.cmpmetadata);
       
      }
    })
  }

  //保存Api元数据
  SaveApiMetadata() {
    if (this.cmpmetadata && this.cmpmetadata.Methods && this.cmpmetadata.Methods.length > 0) {
      if (isNullOrEmptyString(this.cmpmetadata.AssemblyName) || isNullOrUndefined(this.cmpmetadata.AssemblyName)) {
        alert("程序集名称不能为空。");
        return;
      }
      if (isNullOrEmptyString(this.cmpmetadata.FullClassName) || isNullOrUndefined(this.cmpmetadata.FullClassName)) {
        alert("类全名称不能为空。");
        return;
      }
    }
    if (this.cmpmetadata.Methods && this.cmpmetadata.Methods.length > 0) {
      let validResultDict = this.sameMethodCheck();
      if (validResultDict.key == "true") {
        alert(validResultDict.value);
        return;
      }
    }
    let stringcontent = JSON.stringify(this.cmpmetadata.serialize());
    this.metadataDto.content = stringcontent;
    let sessionId=this.cache.get('sessionId');
    this.metadataService.SaveMetadata(this.metadataDto,sessionId).subscribe(response=>{
        if(response.status==200){
           alert("保存成功！");
        }
        else{
          alert("保存失败!");
        }
    })
  }

  dataStateChange(state: DataStateChangeEvent): void {
    this.state = state;
    this.gridData = process(this.assemblyNameData, this.state);
  }

  sameMethodCheck(): ObjectDict<string> {
    let validResultDict = {} as ObjectDict<string>;
    if (this.cmpmetadata.Methods && this.cmpmetadata.Methods.length > 0) {
        for(let i=0;i<this.cmpmetadata.Methods.length;i++){
           let tempIndex=this.cmpmetadata.Methods.findIndex(method=>method.Id!=this.cmpmetadata.Methods[i].Id&&method.Code==this.cmpmetadata.Methods[i].Code);
           if(tempIndex!=(-1)){
            validResultDict.key = "true";
            validResultDict.value = "编号为" + this.cmpmetadata.Methods[i].Code + "的构件操作已存在。";
            return validResultDict;
           }
        }
    }
    validResultDict.key = "false";
    validResultDict.value = "";
    return validResultDict;
  }
}



